'use strict';
import Note from './Note';
import LStorage from './LStorage';
class StickyNotes {
    constructor(canvas) {
        this.canvas = canvas;
        this.isDragging = false;
        this.isEditing = false;
        this.notesZone = canvas.querySelector('#notes-zone');
        this.trashZone = canvas.querySelector('#trash-zone');
        this.store = new LStorage();
        this.addNote = this.addNote.bind(this);
        this.notesZone.addEventListener('mouseup', (event) => {
            if (event.button === 0) {
                if (this.isDragging)
                    return;
                if (!event.target.closest('.note'))
                    this.addNote({
                        position: { x: event.x, y: event.y }
                    }, (note) => this.store.addNote(note.getNoteData()));
            }
        });
        this.onDragSet = this.onDragSet.bind(this);
        this.setNotesFromStorage();
    }
    setNotesFromStorage() {
        const notes = this.store.getNotes();
        notes.forEach(noteData => {
            this.addNote(noteData, note => note.setSize(noteData.size));
        });
    }
    addNote(noteData, cb) {
        const note = new Note(this.notesZone, this.onDragSet, noteData.text, noteData.id, noteData.color);
        note.setPosition({
            x: noteData.position.x,
            y: noteData.position.y
        });
        cb && cb(note);
    }
    onDragSet(isDragging) {
        this.isDragging = isDragging;
    }
}
function main() {
    const canvas = document.querySelector('#canvas');
    new StickyNotes(canvas);
}
document.addEventListener('DOMContentLoaded', main);
